@import url(https://fonts.googleapis.com/css?family=Roboto:400,500i,700&display=swap);
* {
    margin: 0;
    padding: 0;
    outline: 0;
    box-sizing: border-box;
}

body {
    font: 400 14px Roboto, sans-serif;
    background: #f0f0f5;
    -webkit-font-smoothing: antialiased;
}

inout, button, textarea {
    font: 400 18px Roboto, sans-serif;
}

button {
    cursor: pointer;
}

form input {
    width: 100%;
    height: 60px;
    color: #333;
    border: 1px solid #dcdcde;
    border-radius: 8px;
    padding: 0 24px;
}

form textarea {
    width: 100%;
    min-height: 140px;
    height: 60px;
    color: #333;
    border: 1px solid #dcdcde;
    border-radius: 8px;
    padding: 16px 24px;
    line-height: 24px;
    resize: vertical;
}

.button {
    width: 100%;
    height: 60px;
    background: #E02041;
    border: 0;
    border-radius: 8px;
    color: #fff;
    font-weight: bold;
    margin-top: 16px;
    text-align: center;
    text-decoration: none;
    font-size: 18px;
    line-height: 60px;
    transition: -webkit-filter 0.2;
    transition: filter 0.2;
    transition: filter 0.2, -webkit-filter 0.2;
}

.button:hover{
    -webkit-filter: brightness(90%);
            filter: brightness(90%);
}

.back-link {
    display: flex;
    align-items: center;
    margin-top: 40px;
    color: #41414d;
    font-size: 18px;
    text-decoration: none;
    font-weight: 500;
    transition: opacity 0.2s;
}

.back-link svg {
    margin-right: 8px;
}

.back-link:hover {
    opacity: 0.8;
}

@media screen and (min-width: 320px) and (max-width:916px) {
    .back-link {
        justify-content: center;
    }
}
.login-container {
    padding: 40px;
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.login-container section.form {
    width: 100%;
    max-width: 350px;
    margin-right: 30px;
}

.login-container section.form form {
    margin-top: 40px;
}

.login-container section.form form h1{
    font-size: 32px;
    margin-bottom: 32px;
}

.heroes {
    width: 100%;
}

@media screen and (min-width: 320px) and (max-width:916px) {
    .login-container {
        display: grid;
        margin: auto;
        text-align: center;
        justify-content: center;
    }
    .login-container section.form {
        margin: auto;
    }
    .heroes{
        margin: 40px 0 40px 0;
    }
}
.register-container {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.register-container .content {
    width: 100%;
    padding: 96px;
    background: #f0f0f5;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
    border-radius: 8px;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.register-container .content section {
    width: 100%;
    max-width: 380px;
}

.register-container .content section h1 {
    margin: 40px 0 32px;
    font-size: 32px;
}

.register-container .content section p {
   font-size: 18px;
   color: #737388;
   line-height: 32px;
}

.register-container .content form {
    width: 100%;
    max-width: 450px;
 }

 .register-container .content form input {
    margin-top: 8px;
 }

 .register-container .content form .input-group {
    display: flex;
 }

 .register-container .content form .input-group input + input {
    margin-left: 8px;
 }

 @media screen and (min-width: 320px) and (max-width:916px) {
    .register-container .content{
        display: grid;
        text-align: center;
        justify-content: center;
    }
    .register-container .content, 
    .login-container section.form,
    .register-container .content section {
        margin: auto;
    }
    .heroes{
        margin: 40px 0 40px 0;
    }
}





.profile-container {
    width: 100%;
    max-width: 1180px;
    padding: 0 30px;
    margin: 10px auto;
}

.profile-container header {
    display: flex;
    align-items: center;
}

.profile-container header span {
    font-size: 20px;
    margin-left: 24px;
}

.profile-container header img {
    height: 64px;
}

.profile-container header a {
    width: 260px;
    margin-left: auto;
    margin-top: 0;
}

.profile-container header button {
    height: 60px;
    width: 60px;
    border-radius: 4px;
    border: 1px solid #dcdce6;
    background: transparent;
    margin-left: 16px;
    transition: border-color 0.2s;
}

.profile-container header button:hover {
    border-color: #999
}

.profile-container h1 {
    margin-top: 80px;
    margin-bottom: 24px;
}

.profile-container ul {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 24px;
    list-style: none;
}

.profile-container ul li{
    background: #fff;
    padding: 24px;
    border-radius: 8px;
    position: relative;
}

.profile-container ul li button{
    position: absolute;
    right: 24px;
    top: 24px;
    border: 0;
}

.profile-container ul li button:hover{
    opacity: 0.8;
}

.profile-container ul li strong{
    display: block;
    margin-bottom: 16px;
    color: #41414d;
}

.profile-container ul li p + strong{
    margin-top: 32px
}

.profile-container ul li p {
    color: #737380;
    line-height: 21px;
    font-size: 16px;
}

@media screen and (min-width: 320px) and (max-width:916px) {
    .profile-container header {
        display: grid;
        grid-gap: 10px;
        justify-content: center;
    }
    .profile-container header span,
    .profile-container header button {
        margin-left: 0;
    }
    .profile-container header img,
    .profile-container header button {
        margin: auto;
    }
    .profile-container h1,
    .profile-container header {
        text-align: center;
    }
    .profile-container ul {
        grid-template-columns: repeat(1, 1fr);
    }
}


