.newincident {
    width: 100%;
    max-width: 1120px;
    height: 100vh;
    margin: 0 auto;
    display: flex;
    align-items: center;
    justify-content: center;
}

.newincident .content {
    width: 100%;
    padding: 96px;
    background: #f0f0f5;
    box-shadow: 0 0 100px rgba(0, 0, 0, 0.1);
    border-radius: 8px;

    display: flex;
    justify-content: space-between;
    align-items: center;
}

.newincident .content section {
    width: 100%;
    max-width: 380px;
}

.newincident .content section h1 {
    margin: 64px 0 32px;
    font-size: 32px;
}

.newincident .content section p {
   font-size: 18px;
   color: #737388;
   line-height: 32px;
}

.newincident .content form {
    width: 100%;
    max-width: 450px;
 }

 .newincident .content form input,
 .newincident .content form textarea{
    margin-top: 8px;
 }

 @media screen and (min-width: 320px) and (max-width:916px) {
    .newincident .content {
        display: grid;
        text-align: center;
        justify-content: center;
    }
    .newincident .content,
    .newincident .content section{
        margin: auto;
    }
}





